@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
.Todo {
    width: 450px;
    margin: 20px auto;
    background-color: #46529d;
    padding: 20px;
}

.Todo h1 {
    font-size: 24px;
    text-align: center;
    color: #fff;
}
.TodoForm {
    width: 100%;
    text-align: center;
}

.TodoForm input {
    width: 70%;
    height: 35px;
    background-color: rgba(255, 255, 255, 0);
    padding: 0 15px;
    outline:none;
    border: none;
    vertical-align: middle;
    color: #fff;
}

.TodoForm button {
    float: right;
    margin-right: 20px ;
    width: 30px;
    height: 30px;
    color: #fff;
    background-color: #2ebaee;
    border-radius: 50%;
    vertical-align: middle;
    font-size: 14px;
}
.TodoList {
    padding: 20px;
}

.TodoList li {
    padding: 10px 30px 10px 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    position: relative;
}

.TodoList li input[type="checkbox"] {
    display: none;
}

.TodoList li input[type="checkbox"] + label {
    width: 17px;
    height: 17px;
    background-color: #fff;
    cursor: pointer;
    border-radius: 50%;
    float: left;
}

.TodoList li:after {
    display: block;
    clear: both;
    content: '';
}

.TodoList li.complete input[type="checkbox"] + label {
    background-color: #2ebaee;
    float: left;
}

.TodoList li em {
    color: #fff;
    padding: 0 0 0 40px;
    display: block;
    word-break: break-all;
    font-weight: 300;
}

.TodoList li button {
    opacity: 0;
    color: #fff;
    transition: all .2s ease;
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.TodoList li.complete em {
    color: #fff;
    text-decoration: line-through;
}

.TodoList li:hover button {
    display: block;
    opacity: 1;
}
@charset "utf-8";
/*한글 깨지지 않도록 설정*/
/* * <- 전체선택자로 하지 말 것*/
/*여백 초기화*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
}
/*a링크 초기화*/
a { text-decoration: none; color: #222; }
a:hover {color: #2698cb;}

/*폰트 초기화*/
body, input, textarea, select, button, table {
    font-family: 'Poppins', 'Nanum Gothic','Malgun Gothic','맑은 고딕',dotum,'돋움',sans-serif;
    color:#222;    font-size: 13px;    line-height: 1.5;}
    
/*폰트 스타일 초기화*/
em, address {font-style: normal;}

/*블릿기호 초기화*/
dl, ul, li, ol menu {list-style: none;}

/*제목 태그 초기화*/
h1, h2, h3, h4, h5, h6 {font-size: 13px; font-weight: normal;}

/*테두리 초기화*/
img, fieldset {border: 0 none;}

/* 버튼 초기화 */
button { border: 0; background-color: rgba(0,0,0,0); padding: 0;}

/* 테이블 초기화 */
table {border-collapse: collapse; border-spacing:0; }

/*반응형 이미지*/
img, video {width: 100%;}

/* clearfix */
.clearfix{*zoom:1;}
.clearfix:before, .clearfix:after { display: block; content: '';line-height: 0;}
.clearfix:after {clear: both;}

/*IR 효과 : 이미지 대체텍스트*/
/*1. 의미있는 이미지의 대체 텍스트를 제공하는 경우*/
.ir_pm {display: block;overflow: hidden; font-size: 0; line-height: 0; text-indent:-9999px;}
/*2. 의미있는 이미지의 대체 텍스트로 이미지가 없어도 대체 텍스트를 보여주고자 할 때*/
.ir_wa {display: block;overflow: hidden; position: relative; z-index: -1;
width: 100%; height: 100%;}
/*3.대체 텍스트가 아닌 접근성을 위한 숨김 텍스트를 제공할 때*/
.ir_su {overflow: hidden; position: absolute; width: 0;height: 0;line-height: 0;text-indent: -9999px;}


